import { Component, AfterViewInit } from '@angular/core';
import { Router } from "@angular/router";
import Shepherd from 'shepherd.js';

@Component({
  selector: 'app-info-y-evaluacion-estudiante',
  templateUrl: './info-y-evaluacion-estudiante.component.html',
  styleUrls: ['./info-y-evaluacion-estudiante.component.scss']
})
export class InfoYEvaluacionEstudianteComponent {

  texto_consistencia_informe: string = "Indica qu tan relacionados estn los informes del\n" +
    "estudiante con lo que escribi su supervisor.\n" +
    "Para ms informacin, haga click en el botn.";

  texto_consistencia_evaluacion: string = "Indica qu tan relacionada est la evaluacin escrita del\n" +
    "supervisor, con las notas que este mismo le haya puesto.\n" +
    "Para ms informacin, haga click en el botn.";

  texto_interpretacion_nota: string = "Texto que ayuda a entender qu significa el puntaje\n" +
    "de consistencia evaluacin obtenido.\n";

  texto_interpretacion_informes: string = "Texto que ayuda a entender qu significa el puntaje\n" +
    "de consistencia informes obtenido.\n";

  texto_indice_repeticion: string = "Es un valor que indica qu tanto contenido de los informes es texto repetido\n" +
    "Para ms informacin, haga click en el botn.";

  texto_promedio_evaluacion: string = "Es un valor que indica en promedio las aptitudes del estudiante evaluadas por el supervisor";

  constructor(private router: Router) { }

  ngAfterViewInit() {
    const tour = new Shepherd.Tour({
      useModalOverlay: true,
      defaultStepOptions: {
        classes: 'shadow-md bg-purple-dark',
        scrollTo: true,
        cancelIcon: {
          enabled: true
        },

      }
    });
    const botones = [
      {
        classes: 'shepherd-button-secondary',
        text: 'Salir',
        action: tour.cancel
      },
      {
        classes: 'shepherd-button-primary',
        text: 'Atrs',
        action: tour.back
      },
      {
        classes: 'shepherd-button-primary',
        text: 'Siguiente',
        action: tour.next
      }
    ];
    const titulo = 'Gua de evaluacin de estudiante';


    tour.addSteps([
      {
        id: 'intro',
        beforeShowPromise: function () {
          return new Promise(function (resolve) {
            setTimeout(function () {
              window.scrollTo(0, 0);
              resolve(1);
            }, 500);
          });
        },
        buttons: [
          {
            classes: 'shepherd-button-secondary',
            text: 'Salir',
            action: tour.cancel
          },
          {
            classes: 'shepherd-button-primary',
            text: 'Siguiente',
            action: tour.next
          }
        ],
        cancelIcon: {
          enabled: true
        },
        highlightClass: 'highlight',
        title: titulo,
        text: ['Bienvenido al sistema de Evaluacin de Praxus.<br>Con esta gua usted aprender la informacin que Praxus ofrece para evaluar de mejor manera a los estudiantes.'],
      },
      {
        id: '1',
        attachTo: {
          element: '#dataTable',
          on: 'bottom'
        },
        buttons: botones,
        highlightClass: 'highlight',
        title: titulo,
        text: ['En esta tabla se muestra la informacin principal sobre la prcticas de los estudiantes.'],
      },
      {
        id: '2',
        attachTo: {
          element: '#dataTable',
          on: 'bottom'
        },
        buttons: botones,
        highlightClass: 'highlight',
        title: titulo,
        text: ['Datos importantes, como el nombre, rut y otros datos recolectados por nuestro sistema.'],
      },
      {
        attachTo: {
          element: '#infobox',
          on: 'bottom'
        },
        buttons: botones,
        highlightClass: 'highlight',
        title: titulo,
        text: ['Al posar el mouse sobre el botn de informacin, se desplegar una ventana con informacin sobre el dato de la columna.<br>Al hacer clic podr ver informacin extendida sobre aquel dato.'],
      },
      {
        attachTo: {
          element: '#col_evaluar',
          on: 'bottom'
        },
        buttons: botones,
        highlightClass: 'highlight',
        title: titulo,
        text: ['Una vez la prctica haya sido evaluada, usted podr revisar los antecedentes y decidir si aprueba o reprueba esta prctica.'],
      },
      {
        attachTo: {
          element: '#fila',
          on: 'bottom'
        },
        buttons: [
          {
            classes: 'shepherd-button-secondary',
            text: 'Salir',
            action: tour.cancel
          },
          {
            classes: 'shepherd-button-primary',
            text: 'Atrs',
            action: tour.back
          },
          {
            classes: 'shepherd-button-primary',
            text: 'Siguiente vista',
            action: () => {
              window.location.href = "/guias/detalle-estudiante";
            }
          }
        ],
        highlightClass: 'highlight',
        title: titulo,
        text: ['Al hacer clic en una fila usted podr ver los detalles del estudiante.'],
      }
    ]);

    tour.start();
  }

  redirecting() {
    this.router.navigate(["/consistencia"])
  }
}
